body {
    background-color: #f0e4e5;
    color: #747e77;
    font-family: 'playfair display';
    box-sizing: border-box;
}

.header{
    width: 90%;
    position: relative;
    z-index: 1;
    display: block;
    margin: 10px 20px 20px 20px;
    text-align: center;
    padding-top: 30px;
    padding-bottom: 30px;
    border-top: 8px double #c9d2cd;
    border-bottom: 8px double #c9d2cd;
    font-family: 'Petit Formal Script';
    font-size: larger;

}

.header:before{
    content: "";
    position: absolute;
    z-index: -1;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: url("IMAGES/header_beskuren.jpg") center center; 
    opacity: .4;
}

/*BRÖDSMULOR*/
.navigator{
    width: 90%;
    display: flex;
    flex-direction: row;
    margin: 20px auto;
}
ul.breadcrumb{
    padding: 10px 25px;
    list-style: none;
    background-color: #c9d2cd;
} 

ul.breadcrumb li{
    display: inline;
}
ul.breadcrumb li+li:before{
    padding: 10px;
    color: #747e77;
    content: "/\00a0";
}

ul.breadcrumb li a{
    text-decoration: none;
}

ul.breadcrumb li a:hover{
    cursor: pointer;
    font-weight: 700;
}

ul.breadcrumb li a:visited{
    color: #747e77;
}
 li.ur-here{
    font-weight: 700;
 }
